*{
  margin: 0%;
  padding: 0%;

}

body{
  background-color: #fefefe;

}
.shadow-md {
  --tw-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
}
.navbar{
  display: flex;
  padding-left: 18px;
  /* justify-content: center; */
  align-items: center;
  background-color: rgb(0, 27, 58);
  color: rgb(75, 226, 206);
  height: 60px;
  /* border: 2px solid black; */
  margin: 0 0 2.5rem;
}

.react-icons {
  vertical-align: middle;
}

.displaygrid{
  display: grid;
  grid-column: 2;
  grid-template-columns: 1fr 2fr;
  margin: 0 40px 0 40px;
}


.display{
  /* border: 2px solid black; */
  margin:  auto;
  display: flex;
  justify-content: center;
  align-items: center;
  user-select: none;
  overflow-y: hidden;
  
}
  
.text{
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif; */
  /* font-size: 40px; */
  color: white;
  overflow-x: hidden;
  overflow-wrap: break-word;
  overflow-y:hidden;
  
}

.color{
  
  width: 20px;
  height: 24px;
  border: none ;
  padding: 4px 4px;
}

.playground{
  /* border: 2px black solid; */
  padding: 24px;
  background-color: rgb(242, 242, 242);
  border-radius: 8px ;
 
}

.playground h2{
  color: rgb(0, 27, 58);
  text-align: center;
  padding: 0 0 18px;
}


.playground textarea{
  width: 100%;
  font-size: large;
  padding: 4px;
  box-sizing: border-box;
  border: 2px solid #001B3A;
}


.text-field{
  margin: 0%;
  padding: 4px;
  border: 2px solid #001B3A;
}

.size{
  width: 36px;
}
.align{
  display: flex;
  justify-content: center;  
  margin: 10px;
}

.background .bgcolor{
  /* display:  inline-block; */
  display: flex;
  justify-content: center;
  align-items: center;
}
.mar-align{
  margin: 0 2px 0 2px;
  padding: 2px;
  border: #001B3A solid 2px;
}
.align input{
  margin: 0 4px 0 2px;
}

.background{
  padding-top: 18px;
}

.background span{
  color: #001B3A;
  display: block;
  font-size: medium;
  text-align: center;

  
}

.background label{
  font-size: medium;
}

.background .b{
  cursor: pointer;
  border: none;
  font-size: 14px;
  margin: 10px 2px ;
  padding: 4px;
  font-display:inherit;
  color: rgb(47, 148, 242);

}

.centered-label{
  display: flex;
  align-items: center;
  justify-content: center;
  
}

.centered-label button{
  border:black 2px solid;
  border-radius: 4px;
  padding: 4px;
color: black;
cursor: pointer;
}
.by{
  color: rgb(160, 160, 160);
  font-size: 12px;
  padding-top: 4px;
}


@media (max-width: 500px) {
  .navbar{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(0, 27, 58);
    color: rgb(75, 226, 206);
    height: 40px;
    font-size: 10px;
    padding: 0%;
    margin: 0 0 1rem;
    
  }
  
  .displaygrid{
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    justify-content: center;
  }
  
  
  .display{
    display: flex;
    justify-content: center;
    align-items: center;
    user-select: none;
    overflow-y: hidden;
    margin: inherit;
    
    
  }
    
  .text{
    /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif; */
    /* font-size: 40px; */
    color: white;
    overflow-x: hidden;
    overflow-wrap: break-word;
    overflow-y:hidden;
    
  }
  
  .color{
    width: 30px;
    height: 30px;
    /* border: none ; */
    padding: 4px 4px;
  }
  
  .playground{
    margin-top: 1rem;
    padding: 16px;
    background-color: rgb(242, 242, 242);
    border-radius: 8px ;
    /* width: max-content; */
   
  }
  
  .playground h2{
    color: rgb(0, 27, 58);
    text-align: center;
    padding: 0 0 12px;
    font-size: 16px;
  }
  
  .playground textarea{
    width: 100%;
    height: 100px;
    font-size: large;
    padding: 4px;
    box-sizing: border-box;
    border: 2px solid #001B3A;
  }
  
  
  .text-field{
    margin: 0%;
    padding: 4px;
    border: 2px solid #001B3A;
  }
  
  .size{
    width: 36px;
  }
  .align{
    display: flex;
    justify-content:space-around;
    align-items: center;
    margin: 12px 0 12px;  
  }
  .align span{
    margin: 4px;
  }
  
  .mar-align{
    margin: 0 2px 0 2px;
    padding: 2px;
    border: #001B3A solid 2px;
  }
  .align input{
    margin: 0 4px 0 2px;
  }
  
  .background{
    padding-top: 18px;
  }
  
  .background span{
    color: #001B3A;
    display: block;
    font-size: medium;
    text-align: center;
  }

  .background input[type="range"] , label{
    display: none;
  }
  
  .background .b{
    cursor: pointer;
    border: none;
    font-size: 14px;
    margin: 10px 2px ;
    padding: 4px;
    font-display:inherit;
    color: rgb(47, 148, 242);
  
  }
  
  .centered-label{
    display: flex;
    align-items: center;
    justify-content: center;
    
  }
  
  .centered-label button{
    border:black 2px solid;
    border-radius: 4px;
    padding: 4px;
  color: black;
  cursor: pointer;
  }
  .by{
    display: none;
  }
}